<template>
  <Transition name="modal">
    <div class="modal" @click="closeModal">
      <div class="modal__block" @click.stop="">
        <h2 class="modal__block-title">
          {{ edit ? "Изменить заметку" : "Добавить заметку" }}
        </h2>
        <div class="modal__block-inputs">
          <label>
            <span>Title</span>
            <input type="text" v-model.trim="title" placeholder="Title" />
          </label>
          <label>
            <span>Content</span>
            <textarea v-model.trim="text" placeholder="Content"></textarea>
          </label>
        </div>
        <div class="modal__block-btns">
          <button class="modal__block-btn del" @click="closeModal">
            Отмена
          </button>
          <button class="modal__block-btn edit" @click="addNote" v-if="!edit">
            Добавить
          </button>
          <button
            class="modal__block-btn edit"
            @click="editeNote"
            v-if="edit"
          >
            Изменить
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  data() {
    return {
      title: "",
      text: "",
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal");
      this.title = this.text = "";
    },
    addNote() {
      if (this.title != "" && this.text != "") {
        const note = {
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", note);
        this.title = this.text = "";
      }
    },
    editeNote() {
      if(this.title != '' && this.text != '') {
        const editNote = {
          id: this.editedNote.id,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString()
        }
        this.$emit("editedNewNote", editNote)
        this.closeModal()
      }
    },
  },
  props: {
    edit: Boolean,
    editedNote: Object,
  },
};
</script>